.search-container {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.custom-search {
    display: inline-block;
    width: 80%;
    border: none;
}

.custom-button {
    vertical-align: baseline;
    width:20%;
    height: 100%;
    border-radius: 0;
}